    //         teaching_link: '',
    //     }
    // });
    // return await modal.present();
    if (this.isSharingActive[index] === true) {
      this.socialSharing.share(url).then(
          (res) => {
            if(res) {
              this.isSharingActive[index] === false;
            }
          },
          (err) => {
            if(err) {
              this.isSharingActive[index] === false;
            }
          }
      );
    }
